import { FunctionComponent } from 'preact';

import { PageInfo } from '@/requisitionList/data/models';
import { DEFAULT_PAGE_SIZE } from '@/requisitionList/lib/constants';
import { useText } from '@adobe-commerce/elsie/i18n';

import '@/requisitionList/components/PaginationItemsCounter/PaginationItemsCounter.css';

export interface PaginationItemsCounterProps {
  pageInfo?: PageInfo;
  totalCount?: number;
  className?: string;
}

export const PaginationItemsCounter: FunctionComponent<
  PaginationItemsCounterProps
> = ({ pageInfo, totalCount, className = '' }) => {
  const translations = useText({
    itemsCounter: `RequisitionList.PaginationItemsCounter.itemsCounter`,
  });

  // Don't show counter if no pageInfo or if there's no total count
  if (!pageInfo || !totalCount) {
    return null;
  }

  const pageSize = pageInfo.page_size ?? DEFAULT_PAGE_SIZE;
  const currentPage = pageInfo.current_page ?? 1;
  const currentPageSize = pageSize * currentPage;
  const total = totalCount;

  const from = currentPageSize - pageSize + 1;
  const to = currentPageSize > total ? total : currentPageSize;

  return (
    <span className={`pagination-items-counter ${className}`.trim()}>
      {translations.itemsCounter
        .replace('{from}', from.toString())
        .replace('{to}', to.toString())
        .replace('{total}', total.toString())}
    </span>
  );
};
