import { FunctionComponent } from 'preact';
import { Icon, Button } from '@adobe-commerce/elsie/components';
import { classes } from '@adobe-commerce/elsie/lib';
import { Search } from '@adobe-commerce/elsie/icons';
import '@/requisitionList/components/NotFound/NotFound.css';

export interface NotFoundProps {
  className?: string;
  title?: string;
  message?: string;
  actionLabel?: string;
  onAction?: () => void;
}

export const NotFound: FunctionComponent<NotFoundProps> = ({
  className,
  title = '404 - Not Found',
  message = 'The requisition list you are looking for does not exist.',
  actionLabel,
  onAction,
  ...props
}) => {
  return (
    <div
      className={classes(['not-found', className])}
      data-testid="not-found"
      {...props}
    >
      <Icon source={Search} size={'64'} stroke={'2'} />
      <h2>{title}</h2>
      {message && <p>{message}</p>}
      {actionLabel && onAction && (
        <Button variant="primary" onClick={onAction}>
          {actionLabel}
        </Button>
      )}
    </div>
  );
};
