import { HTMLAttributes } from 'preact/compat';
import { Picker } from '@adobe-commerce/elsie/components';
import { useText } from '@adobe-commerce/elsie/i18n';
import '@/requisitionList/components/PageSizePicker/PageSizePicker.css';

export interface PageSizePickerProps extends HTMLAttributes<HTMLDivElement> {
  currentPageSize: number;
  onPageSizeChange: (pageSize: number) => void;
  disabled?: boolean;
  pageSizeOptions?: number[];
}

export const PageSizePicker = ({
  currentPageSize,
  onPageSizeChange,
  disabled = false,
  pageSizeOptions = [10, 25, 50, 100],
}: PageSizePickerProps) => {
  const translations = useText({
    itemsPerPage: `RequisitionList.PageSizePicker.itemsPerPage`,
  });

  const handlePageSizeChange = (event: Event) => {
    const target = event.target as HTMLSelectElement;
    const newPageSize = parseInt(target.value, 10);
    onPageSizeChange(newPageSize);
  };

  const options = pageSizeOptions.map((size) => ({
    value: size.toString(),
    text: size.toString(),
  }));

  return (
    <Picker
      disabled={disabled}
      data-testid="page-size-picker"
      variant="primary"
      size="medium"
      value={currentPageSize.toString()}
      options={options}
      handleSelect={handlePageSizeChange}
      aria-label={translations.itemsPerPage}
    />
  );
};
